*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar-bg{
  background-color: var(--bgcolor);
 z-index: 99;
 position: relative;
}
.navbar-brand{
  font-size: 2.5rem;
}
.navbar li{
  padding: 0 2 rem;
}
.navbar li a {
  font-size: 1.5rem;
  font-weight: lighter;
  position: relative;
}
.navbar li a:hover{
  color: var(--primary-color)!important;
  text-shadow: 0 0.2rem 0.3rem rgba(183,171, 255, 0.5);
}
.btn-style{
  width: 12rem;
  height: 2rem;
  border-radius: 5%;
  color:black;
  background-color: violet!important;
  text-transform: capitalize;
  font-size: 1rem;
}
.btn-style:first-child{
  margin-right: 2rem;
}
.btn-style-border:hover{
  border: 0.1rem solid var(--primary-color)!important;
  background-color:black!important;
  color: white;
 box-shadow: 0 0.2rem 0.3rem rgba(183,171, 255, 0.5);
}
.btn-style-border{
  border: 0.1rem solid var(--primary-color);
  background-color: transparent;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.3rem rgba(183,171, 255, 0.5);
}
.btn-style-border{
  color:#fff;
  background-color:#00EA64;
  margin: 1%;
  text-transform: capitalize;
  width: 11rem;
}
header{
  background-color: aquamarine;
  min-height: 100vh;
}
.main-hero-container{
  height: 80vh;
  display: flex;
}
.main-hero-container h1{
  text-transform: capitalize;
  font-weight: bolder;
  margin: 2rem;
  font-size: 2.5rem;
}
.main-hero-para{
  font-size: 1rem;
  margin: 2rem;
  color:black;
}
.main-hero-parac {
  color: #fff;
}
.main-hero-container h3{
  margin: 2rem;
font-weight: lighter;
}
.main-herosection-images{
  position: relative;

}
.main-herosection-images  img{
  width: 60%;
  height: 75%;
  margin-top: 10rem;
  box-shadow: 0 0.8rem 0.8rem rgba(0,0,0,0.25);
  border-radius: 1.5rem;
  position: absolute;
}
.main-herosection-images.main-hero-img2{
  position: absolute;
  width: 50%;
  height: 30%;
  left: 55%;
  top: -2%;
}
.input-group{
  position: relative!important;
}
.form-control-text{
  position: absolute;
  height: 3rem;
  border: none;
  outline-style: none;
  padding-left: 1 rem !important;
  font-size: 1.4rem;
  font-weight: lighter;
}
.input-group-button{
  position: absolute;
  right: 20%;
  top:0.rem;
  color: #fff;
  background-color: #00EA64;
  height: 3rem;
  width: 20%;
  border-radius: 1rem!important;
  text-align: center;
  padding: 2%;
}
footer{
  margin-top: 5%;
  padding: 2rem 0;
  background-color: black;
  color:#fff;
}
footer h2{
  color: #fff;
}
footer li a{
  list-style: none;
  font-size: 1 rem;
  font-weight: lighter;
  color: #fff;
  cursor: pointer;
  margin: 1rem;
  text-decoration: none;
}
footer .main-hero-para {
  margin: 0;
}